import fetchobj from '@/utils/fetch'
let fetch = fetchobj.fetch;

export const Project = {

    createMetting(data) {
        return fetch('post', '/createMetting', data)
    },

    getMetting(data) {
        return fetch('get', '/mettingList', data)
    },

    createDemand(data) {
        return fetch('post', '/createDemand', data)
    },

    getDemand(data) {
        return fetch('get', '/demandList', data)
    },

    getTag(data) {
        return fetch('get', '/tag', data)
    },
    createTag(data) {
        return fetch('post', '/tag', data)
    },
    updateTag(data) {
        return fetch('put', '/tag', data)
    },
    deleteTag(data) {
        return fetch('delete', '/tag', data)
    },

}
