<template>
  <div class="app-container">
    <div class="filter-container">
      <el-date-picker class="filter-item" clearable v-model="listQuery.createTime" type="date"></el-date-picker>
      <el-select class="filter-item" v-model="listQuery.tag" placeholder="版本号">
        <el-option
          v-for="item in tagList"
          :key="item._id"
          :label="item.tagName"
          :value="item.tagName"
        ></el-option>
      </el-select>
      <el-button
        class="filter-item"
        style="margin-left:20px"
        type="primary"
        icon="el-icon-search"
        @click="search"
      >查询</el-button>
    </div>
    <div class="article-table">
      <el-table
        v-loading="listLoading"
        :data="dataList"
        fit
        :show-header="false"
        style="width: 100%"
      >
        <el-table-column width="130px" align="center" label="创建时间">
          <template slot-scope="scope">
            <el-tag type="warning">
              <i class="el-icon-time"></i>
              <span>{{ scope.row.createTime | parseTime('{y}-{m}-{d}') }}</span>
            </el-tag>
          </template>
        </el-table-column>

        <el-table-column width="30px" align="center">
          <template slot-scope="scope">
            <div class="article-timeline"></div>
          </template>
        </el-table-column>

        <el-table-column width="100px" align="left" label="版本">
          <template slot-scope="scope">
            <el-tag>{{ scope.row.tag }}</el-tag>
          </template>
        </el-table-column>

        <el-table-column width="700px" align="center" label="内容">
          <template slot-scope="scope">
            <el-card v-for="(item,index) in scope.row.children" :key="index" class="box-card">
              <div slot="header" class="clearfix">
                <span style="float:left">
                  <!-- {{`云课堂${scope.row.tag}需求文档`}} -->
                  <el-tag v-if="item.type=='0'" type>新增需求</el-tag>
                  <el-tag v-if="item.type=='1'" type="warning">需求变更</el-tag>
                  <el-tag v-if="item.type=='2'" type="danger">临时需求</el-tag>

                  <el-tag type="info">{{item.author}}</el-tag>
                </span>

                <el-button
                  type="primary"
                  @click="changeDemand({item,tag:scope.row.tag})"
                  style="float: right;"
                  size="mini"
                >变更</el-button>
              </div>
              <div class="article-content" v-html="item.content"></div>
            </el-card>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination
      style="margin-top:0px;"
      v-show="total>10"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
    />
  </div>
</template>

<script>
import { Project } from "@/api/project";
import Pagination from "@/components/Pagination"; // Secondary package based on el-pagination
import { mapGetters } from "vuex";
export default {
  name: "ArticleList",
  components: { Pagination },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  data() {
    return {
      tagList: [],
      currentName: null,
      dataList: [],
      total: 0,
      listLoading: true,
      listQuery: {
        tag: "",
        createTime: "",
        page: 1,
        limit: 10
      }
    };
  },
  created() {
    this.getList();
    this.getTag();
  },
  methods: {
    getTag() {
      Project.getTag({
        limit: 1000,
        page: 1
      }).then(res => {
        this.tagList = res.data;
      });
    },
    search() {
      this.listQuery.page = 1;
      this.getList();
    },
    getList() {
      this.listLoading = true;
      let params = JSON.parse(JSON.stringify(this.listQuery));
      Project.getDemand(params).then(response => {
        response.data.forEach(element => {
          element.children.reverse();
        });
        this.dataList = response.data;
        this.total = response.total;
        this.listLoading = false;
      });
    },
    changeDemand(obj) {
      window.localStorage.setItem("demand", JSON.stringify(obj));
      this.$router.push({
        path: "/project/createDemand",
        query: {
          change: 1
        }
      });
    },
    handleSizeChange(val) {
      this.listQuery.limit = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    }
  }
};
</script>


